import { Component, OnInit } from '@angular/core';
import { AppService } from './../app.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-contact-list',
  templateUrl: './contact-list.component.html',
  styleUrls: ['./contact-list.component.css']
})
export class ContactListComponent implements OnInit {

  selectedEmail: string = 'E-mail Address';
  selectedWebPage: string ='Web Page';
  selectedFax: string = 'Home Fax';
  mobileStatus: boolean = false;

  public usersDetails = [];
  public searchUser: any;
  public DisplayName = [];
  public singleUser = false;
  public singleUserDetails: any;

  public countries = [];
  public countriesCode = [];
  public singleCountryCode: any;
  public countryCode: any;
  public countryName: string;

  /*Single user details*/

  public singleUserName: string;

  constructor(public appService: AppService, public _route: ActivatedRoute, public router: Router) { }

  ngOnInit() {
    this.appService.getUserDetails().subscribe(
      (response) => {
        for (let user in response) {
          let singleUser = response[user]
          let userDetail = [
            {
              id: user,
              ...singleUser
            }
          ]
          this.usersDetails.push(userDetail[0])
        }
      }
    )
  }

  public noUserSelected : any = () => {
    this.mobileStatus = false;
    this.singleUser = false
  }

  public selectedUserDetail: any = (id) => {
    this.mobileStatus = true;
    this.singleUser = true;
    this.singleUserDetails = this.usersDetails[id];
    console.log(this.singleUserDetails);
  }

  selectEmailChangeHandler(event:any){
    this.selectedEmail = event.target.value;
  }

  selectWebPageChangeHandler(event:any){
    this.selectedWebPage = event.target.value;
  }

  selectFaxChangeHandler(event:any){
    this.selectedFax = event.target.value;
  }

}